<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code>tileset json 文件地址。</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre="">optional</code>是否显示 tileset 模型。</td>
</tr>
<tr>
<td>modelMatrix</td>
<td>Matrix4</td>
<td>Matrix4.IDENTITY</td>
<td><code v-pre="">optional</code> 一个 4x4 变换矩阵，用于转换 tileset 的根块。</td>
</tr>
<tr>
<td>shadows</td>
<td>ShadowMode</td>
<td>ShadowMode.ENABLED</td>
<td><code v-pre="">optional</code> 确定 tileset 是否投射或接收来自每个光源的阴影。</td>
</tr>
<tr>
<td>maximumScreenSpaceError</td>
<td>Number</td>
<td>16</td>
<td><code v-pre="">optional</code> 用于驱动细节细化级别的最大屏幕空间错误。</td>
</tr>
<tr>
<td>maximumMemoryUsage</td>
<td>Number</td>
<td>512</td>
<td><code v-pre="">optional</code> tileset 可以使用的最大内存量（MB）。</td>
</tr>
<tr>
<td>cullWithChildrenBounds</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre="">optional</code> 优化选项。 是否使用子绑定卷的并集来剔除切片。</td>
</tr>
<tr>
<td>dynamicScreenSpaceError</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 优化选项。 减少远离相机的瓷砖的屏幕空间错误。</td>
</tr>
<tr>
<td>dynamicScreenSpaceErrorDensity</td>
<td>Number</td>
<td>0.00278</td>
<td><code v-pre="">optional</code> 用于调整动态屏幕空间误差密度，类似于雾密度。</td>
</tr>
<tr>
<td>dynamicScreenSpaceErrorFactor</td>
<td>Number</td>
<td>4.0</td>
<td><code v-pre="">optional</code> 用于增加计算的动态屏幕空间错误的因子。</td>
</tr>
<tr>
<td>dynamicScreenSpaceErrorHeightFalloff</td>
<td>Number</td>
<td>0.25</td>
<td><code v-pre="">optional</code> tileset 高度开始下降的比率。</td>
</tr>
<tr>
<td>skipLevelOfDetail</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre="">optional</code> 优化选项。 确定在遍历期间是否应该应用详细信息跳过级别。</td>
</tr>
<tr>
<td>baseScreenSpaceError</td>
<td>Number</td>
<td>1024</td>
<td><code v-pre="">optional</code> 当 skipLevelOfDetail 为 true 时，在跳过详细级别之前必须达到的屏幕空间错误。</td>
</tr>
<tr>
<td>skipScreenSpaceErrorFactor</td>
<td>Number</td>
<td>16</td>
<td><code v-pre="">optional</code> 当 skipLevelOfDetail 为 true 时，定义要跳过的最小屏幕空间错误的乘数。 与 skipLevels 结合使用以确定要加载的切片。</td>
</tr>
<tr>
<td>skipLevels</td>
<td>Number</td>
<td>1</td>
<td><code v-pre="">optional</code>当 skipLevelOfDetail 为 true 时，一个常量定义加载切片时要跳过的最小级别数。 当它为 0 时，不会跳过任何级别。与 skipScreenSpaceErrorFactor 结合使用以确定要加载的切片。</td>
</tr>
<tr>
<td>immediatelyLoadDesiredLevelOfDetail</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 当 skipLevelOfDetail 为 true 时，将只下载满足最大屏幕空间错误的切片。跳过因子将被忽略，并且只加载所需的切片。</td>
</tr>
<tr>
<td>loadSiblings</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 当 skipLevelOfDetail 为 true 时，确定在遍历期间是否始终下载可见切片的兄弟节点。</td>
</tr>
<tr>
<td>clippingPlanes</td>
<td>ClippingPlaneCollection</td>
<td></td>
<td><code v-pre="">optional</code> ClippingPlaneCollection 用于有选择地禁用渲染 tileset。</td>
</tr>
<tr>
<td>classificationType</td>
<td>ClassificationType</td>
<td></td>
<td><code v-pre="">optional</code> 确定此 tileset 是否会对 terrain，3D Tiles 或两者进行分类。 有关限制和限制的详细信息，请参阅 Cesium3DTileset＃classificationType。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Ellipsoid</td>
<td>Ellipsoid.WGS84</td>
<td><code v-pre="">optional</code> 决定地球的大小和形状参考椭球体。</td>
</tr>
<tr>
<td>pointCloudShading</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 用于构造 PointCloudShading 对象以基于几何误差和光照控制点衰减的选项。</td>
</tr>
<tr>
<td>imageBasedLightingFactor</td>
<td>Cartesian2</td>
<td>new Cartesian2(1.0, 1.0)</td>
<td><code v-pre="">optional</code> 地球、天空、大气层的光照缩放因子。</td>
</tr>
<tr>
<td>lightColor</td>
<td>Cartesian3</td>
<td></td>
<td><code v-pre="">optional</code> 模型阴影的颜色和强度。</td>
</tr>
<tr>
<td>debugFreezeFrame</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，确定是否只使用最后一帧的切片进行渲染。</td>
</tr>
<tr>
<td>debugColorizeTiles</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则给每个 tile 一个随机颜色。</td>
</tr>
<tr>
<td>debugWireframe</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用， 如果为 ture，则渲染每个 tile content 为线框。</td>
</tr>
<tr>
<td>debugShowBoundingVolume</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则渲染每个 tile 的边界体积。</td>
</tr>
<tr>
<td>debugShowContentBoundingVolume</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则渲染每个 tile content 的边界体积。</td>
</tr>
<tr>
<td>debugShowViewerRequestVolume</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则渲染每个 tile 的请求量。</td>
</tr>
<tr>
<td>debugShowGeometricError</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则绘制标签表示每个 tile 的几何误差。</td>
</tr>
<tr>
<td>debugShowRenderingStatistics</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则绘制标签以表示每个 tile 的 commonds、points、triangles、features 的数量。</td>
</tr>
<tr>
<td>debugShowMemoryUsage</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则绘制标签表示每个 tile 的纹理和几何内存，以 mb 为单位。</td>
</tr>
<tr>
<td>debugShowUrl</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> 仅调试可用，如果为 true，则绘制标签表示每个 tile 的网址。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/Cesium3DTileset.html">Cesium3DTileset</a></strong></li>
</ul>
<h2>事件</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>allTilesLoaded</td>
<td></td>
<td>该组件所有层级的 tiles 加载完毕后触发该事件。</td>
</tr>
<tr>
<td>initialTilesLoaded</td>
<td></td>
<td>该组件所有初始层级的 tiles 加载完毕触发该事件。</td>
</tr>
<tr>
<td>loadProgress</td>
<td>(numberOfPendingRequests, numberOfTilesProcessing)</td>
<td>加载 tiles 触发，指示 tile 加载进度。</td>
</tr>
<tr>
<td>tileFailed</td>
<td>{url, message}</td>
<td>tile 加载失败时触发该事件。</td>
</tr>
<tr>
<td>tileLoad</td>
<td></td>
<td>tile 加载完成后触发该事件。</td>
</tr>
<tr>
<td>tileUnload</td>
<td></td>
<td>tile 加载未成功加载时触发该事件。</td>
</tr>
<tr>
<td>tileVisible</td>
<td></td>
<td>tile 可见性发生改变时触发该事件。</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          uri: './statics/SampleData/Cesium3DTiles/Tilesets/Tileset/tileset.json',
          id: 'Hello Vue Cesium',
          description: 'This is a 3DTilesetGraphics',
          position: { lng: 108, lat: 35, height: 5000 },
          orientation: {}
        }
      },
      mounted () {
        this.$refs.entity.createPromise.then( ({Cesium, viewer, cesiumObject}) => {
          console.log('123')
          viewer.zoomTo(cesiumObject)
        })
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          const { Cesium, viewer } = cesiumInstance
          var position = Cesium.Cartesian3.fromDegrees(this.position.lng, this.position.lat, this.position.height);
          var heading = Cesium.Math.toRadians(90)
          var pitch = Cesium.Math.toRadians(45)
          var roll = Cesium.Math.toRadians(45)
          var hpr = new Cesium.HeadingPitchRoll(heading, pitch, roll)
          this.orientation = Cesium.Transforms.headingPitchRollQuaternion(position, hpr);
        }
      }
    }
  </script>